<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
    const blob = new Blob([JSON.stringify({a: "aa"})], {type: 'application/json'});
    `,
    `
    const arr = new Int8Array([1,2,3,4]);
    const blob = new Blob([arr.buffer], {type: 'application/octet-stream'});
    const url = URL.createObjectURL(blob); // 如果buffer是可以被标签引用的，可以直接将url传入即可

    // 当引用完资源需要释放引用
    URL.revokeObjectURL(url); 
    `,
    `
    const fr = new FileReader();
    fr.addEventListener('loadend', () => {
        console.log(fr.result);
    })
    fr.readAsArrayBuffer(blob);

    或者
    new Response(blob, {
        "status" : 200 , 
        "statusText" : "OK!"
    }).arrayBuffer().then(res => {
        console.log(res);
    });
    `,
    `
    当大文件需要分块传输时候非常有用，slice传入开始索引和结束索引(不包括)
    const newBlob = blob.slice(0, 1, 'application/octet-stream');
    `,
    `
    const file = new File(["js太强了"], "test.txt", {
        type: 'text/plain',
        lastModified: Date.now()
    })
    `,
    `
    简单的表单提交案例

    form.addEventListener('submit', e => {
        e.preventDefault();

        Array.from(form.elements).forEach(element => {
            if(element.nodeName === "INPUT" && element.type === "file"){
                const files = element.files;
                for(let i = 0; i < files.length; ++i){
                    const reader = new FileReader();

                    reader.onloadstart = () => {};
                    
                    reader.onload = e => { console.log(e.target.result); };

                    reader.onloadend = () => {};

                    reader.onprogress = e => { console.log(e.type, e.loaded)  };

                    reader.onabort = function() { console.log('终止reader'); };

                    reader.onerror = () => { reader.abort() };

                    reader.readAsText(files.item(i));
                    /**
                     * 1. readAsArrayBuffer
                     * 2. readAsBinaryString 作用和readAsArrayBuffer一样
                     * 3. readAsDataURL 资源转成base64位字符串
                     * 4. readAsText(blob, 'UTF-8') 资源转成文本字符串
                    */
                }
            }
        })
    })
    `
],
